export default({
		data() {
			return {
				collapsed: false,
				sysUserName:'王某某',
				sysCol: '#000000',
				predefineColors: [
					'#ff4500',
					'#ff8c00',
					'#ffd700',
					'#90ee90',
					'#00ced1',
					'#1e90ff',
					'#c71585',
					'#bae1eb',
					'rgba(255, 69, 0, 0.68)',
					'rgb(255, 120, 0)',
					'hsv(51, 100, 98)',
					'hsva(120, 40, 94, 0.5)',
					'hsl(181, 100%, 37%)',
					'hsla(209, 100%, 56%, 0.73)',
					'#c7158577'
				],
				sysUserAvatar: '../assets/nipic.png',
				scaleClass: '',	
				loading:false,
				menus: [
					{
						'iconCls': 'iconfont icon-shangpin',
						'name': '主菜单',
						'childrens': [{
							'path': '/Nav1/MerchandiseTable',
							'name': '商品信息表'
						},
						{
							'path': '/Nav1/zhizhibao',
							'name': '值值宝'
						},
						{
							'path': '/404',
							'name': '404'
						},
						{
							'path': '/500',
							'name': '500'
						}
						]
					}
				],
				breadList:[
					
				]
			}
		},
		watch: {
            $route() {
                this.getBread()
            }
       },
		created() {
			if(localStorage.sysBkCol==undefined){
				this.sysCol =this.sysCol;
				localStorage.sysBkCol = this.sysCol;
			}else{
				this.sysCol = localStorage.sysBkCol;
			}
			this.getBread();
			
			
		},
		mounted(){
			this.scaleClass="scaleClass"
		},
		methods: {
			isCollapsed() {
				this.collapsed = !this.collapsed;
			},
			logout() {
				this.$confirm('确定退出？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
				}).
				then(() => {
					this.$message({
						type: 'success',
						message: '已退出当前系统'
					})
					this.$router.push({
						path: '/Login'
					});
				}).
				catch(() => {
					this.$message({
						type: 'info',
						message: '已取消当前操作'
					})
				})
			},
			chickColorPicker(value) {
				if(value == null) {
					this.sysCol = localStorage.sysBkCol;
				}else{
					this.sysCol = value;
					localStorage.sysBkCol = value;
				}
			},
			handleopen(){},
			handleclose(){},
			handleselect(){
				this.loading=true;
				window.setTimeout(()=>{ this.loading=false;}, 1000);
			},
            getBread(){
				let matched=this.$route.matched.filter(item=>item.name)//$route.matched 将会是一个包含从上到下的所有对象 (副本)。
				//const first=matched[0]
	            //if(first && first.name !=='dashboard'){//$route.name当前路由名称  ；$route.redirectedFrom重定向来源的路由的名字
	            //  matched=[{ path: '/', meta: { title: '当前页' }}].concat(matched)
	            //}
	            for(var i=1;i<matched.length;i++){
	            	this.breadList.push(matched[i])
	            }
	            this.breadList=matched
				}
		},
		watch: {
            $route() {
            	//router.go(0)
            	//监听路由地址变化，重新渲染面包屑
                this.getBread()
            }
        },
	})


