<template>
	<el-row class="container" :class="scaleClass">
		<el-col :span="24" class="header" :style="{ background: sysCol}">
			<el-col :span="3" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				<template v-if="collapsed">
				</template>
				<template v-else>
					<img src="../../static/images/slio.png" />					
				</template>
			</el-col>
			<el-col :span="3">
				<div class="switchover">
					<i style="cursor:pointer;" class="iconfont icon-caidan" @click.prevent="isCollapsed"></i>
				</div>
			</el-col>
			<el-col :span="18" class="userinfo">
				<el-color-picker v-model="sysCol" size="small" :predefine="predefineColors" @change="chickColorPicker" class="colorPicker"></el-color-picker>
				<el-dropdown>
					<span class="el-dropdown-link"><!--<img :src="sysUserAvatar" />--> <img src="../../static/images/nipic.png" />{{sysUserName}}</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>个人资料</el-dropdown-item>
						<el-dropdown-item><el-badge is-dot>我的消息</el-badge></el-dropdown-item>
						<el-dropdown-item>设置</el-dropdown-item>
						<el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col class="main el-col-24" >
			<aside :class="collapsed?'menu-collapsed':'menu-expanded'">							
				<el-menu 
					class="el-menu " 
					mode="vertical"
					background-color="#eef1f6" 
					text-color="#3e3f42" 					
					active-text-color="#409EFF"  
					:collapse="collapsed" 							
					unique-opened
					@select="handleselect" 
					:default-active="$route.name"
					router  
					>					
					<el-submenu v-for="(menu, idx) in menus" :index="idx+''" >						
					    <template slot="title">
					      <i :class="menu.iconCls"></i>
					      <span>{{menu.name}}</span>
					    </template>
						<el-menu-item v-for="children in menu.childrens" :index="children.path" >{{children.name}}</el-menu-item>
					</el-submenu>				
				</el-menu>
			</aside>
			<section class="content-container el-col-24"  v-loading="loading">
			    <el-breadcrumb separator-class="el-icon-arrow-right" class="separator">
			    	<el-breadcrumb-item>当前页</el-breadcrumb-item>
				  	<el-breadcrumb-item  v-for="bread in breadList">
				  		 <router-link :to="bread.path">{{bread.meta.title}}</router-link>
				  	</el-breadcrumb-item>
				</el-breadcrumb>
				<router-view/>
			</section>
	</el-col>
	</el-row>
</template>
<script>
  import Main from '../assets/js/Main.js'
  export default Main
</script>
<style scoped lang="scss" >
@import '../assets/css/Main.scss'
</style>



